html.dialog-open {
  overflow: hidden;
}

html.dialog-open #main {
  padding-right: 23px;
}

#dialogOverlay {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 21000;
  overflow: auto;
}

#dialogBox {
  font-family: 'Segoe UI', 'Open Sans', sans-serif;
  position: relative;
  top: 40%;
  left: 40%;
  min-width: 150px;
  z-index: 21001;
  /* Colors */
}

#dialogBox .blue-bg {
  background-color: #07f;
}

#dialogBox .darkblue-bg {
  background-color: #03d;
}

#dialogBox .green-bg {
  background-color: #090;
}

#dialogBox .red-bg {
  background-color: #d00;
}

#dialogBox .orange-bg {
  background-color: #d50;
}

#dialogBox .yellow-bg {
  background-color: #b80;
}

#dialogHeader {
  font-weight: 200;
  width: auto;
  padding: 5px 8px;
  font-size: 18px;
  color: #fff;
  background: #ededed linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.4) 100%);
}

#dialogContent {
  padding: 15px 10px;
  font-size: 16px;
  font-weight: normal;
  /* Colors */
}

#dialogContent form > label > span ~ select,
#dialogContent form > label > span ~ textarea,
#dialogContent form > label > span ~ input:not([type=radio]):not([type=checkbox]):not([type=button]) {
  margin-top: 5px !important;
  display: block;
}

#dialogContent form > label > select,
#dialogContent form > label > textarea,
#dialogContent form > label > input:not([type=radio]):not([type=checkbox]):not([type=button]) {
  width: 100%;
  box-sizing: border-box;
}

#dialogContent .notice {
  padding: 4px 7.5px;
  font-size: .9em;
  border-width: 1px;
}

#dialogContent p {
  margin: 0 0 5px;
}

#dialogContent p code {
  background-color: rgba(0, 0, 0, 0.1);
  color: #000;
  font-size: .9em;
  padding: 3px;
  border-radius: 3px;
}

#dialogContent > div:not(:first-child):not(#dialogButtons) {
  border-top: 1px solid;
  padding-top: 5px;
  margin-top: 4px;
}

#dialogContent > *:not(p) + p {
  margin-top: 5px;
}

#dialogContent > *:last-child {
  margin-bottom: 0;
}

#dialogContent .blue {
  color: #07f;
}

#dialogContent .darkblue {
  color: #03d;
}

#dialogContent .green {
  color: #090;
}

#dialogContent .red {
  color: #d00;
}

#dialogContent .orange {
  color: #d50;
}

#dialogContent .yellow {
  color: #b80;
}

#dialogButtons {
  width: auto;
  font-size: 18px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  margin-top: 15px;
}

#dialogButtons:empty {
  display: none;
}

#dialogButtons button, #dialogButtons input {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  min-width: 90px;
  min-height: 32px;
  height: 32px;
  color: #fff;
  margin: 0 10px 0 0 !important;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  padding: 4px 10px;
  position: relative;
  outline: none;
  opacity: .85;
  border: 3px solid transparent;
  box-sizing: border-box;
}

#dialogButtons button[disabled], #dialogButtons input[disabled] {
  cursor: not-allowed;
}

#dialogButtons button:last-child, #dialogButtons input:last-child {
  margin-right: 0 !important;
}

#dialogButtons button:focus, #dialogButtons input:focus {
  opacity: 1;
  border-color: rgba(0, 0, 0, 0.2);
}

#dialogButtons button:active, #dialogButtons input:active {
  top: 1px;
  left: 1px;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]),
select,
textarea {
  border: 1px solid;
  background-color: #efefef;
  color: #313131;
  padding: 4px;
  outline: 0;
  min-height: 18px;
  position: relative;
  line-height: 16px;
  font-size: 14px;
  transition: color 0.2s, border-color 0.2s;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]):focus,
select:focus,
textarea:focus {
  border-color: #07f;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:focus:invalid,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:focus:invalid,
select:not(select)[required]:focus:invalid,
select:not(select)[pattern]:focus:invalid,
textarea:not(select)[required]:focus:invalid, textarea:not(select)[pattern]:focus:invalid {
  border-color: #d00;
  color: #a00;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:focus:valid,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:focus:valid,
select:not(select)[required]:focus:valid,
select:not(select)[pattern]:focus:valid,
textarea:not(select)[required]:focus:valid, textarea:not(select)[pattern]:focus:valid {
  border-color: #0d0;
  color: #0a0;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:not(:focus) :valid:hover,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:not(:focus) :valid:hover,
select:not(select)[required]:not(:focus) :valid:hover,
select:not(select)[pattern]:not(:focus) :valid:hover,
textarea:not(select)[required]:not(:focus) :valid:hover, textarea:not(select)[pattern]:not(:focus) :valid:hover {
  border-color: #0a0;
  color: #0d0;
}

input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:not(:focus) :invalid:hover,
input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:not(:focus) :invalid:hover,
select:not(select)[required]:not(:focus) :invalid:hover,
select:not(select)[pattern]:not(:focus) :invalid:hover,
textarea:not(select)[required]:not(:focus) :invalid:hover, textarea:not(select)[pattern]:not(:focus) :invalid:hover {
  border-color: #a00;
  color: #d00;
}

form:hover textarea[required]:focus:valid, form:hover textarea[pattern]:focus:valid {
  border-color: #0d0;
}

select {
  padding-right: 5px;
}

textarea {
  padding-right: 5px;
  min-height: 100px;
}

input[type=checkbox] {
  -webkit-appearance: none;
  display: inline-block;
  border: 1px solid #888;
  background-color: #EFEFEF;
  width: 16px;
  height: 16px;
  border-radius: 3px;
  position: relative;
  margin: 0 3px 0 0;
  vertical-align: bottom;
  top: -2px;
}

input[type=checkbox]:checked:before {
  opacity: 1;
}

input[type=checkbox]:before {
  position: absolute;
  top: 2px;
  left: 2px;
  content: "\2713";
  font-size: 14px;
  line-height: 10px;
  opacity: 0;
  color: #000;
}

#dialogContent > div:not(#dialogButtons) > form[id] > * {
  display: block;
  margin: 0 auto 10px;
  font-family: inherit;
  width: 100%;
  box-sizing: border-box;
}

#dialogContent > div:not(#dialogButtons) > form[id] > *:last-child {
  margin-bottom: 0;
}

#dialogContent > div:not(#dialogButtons) > form[id] label {
  text-align: center;
  cursor: pointer;
}

#dialogContent > div:not(#dialogButtons) > form[id] label > * {
  vertical-align: text-bottom;
}

#dialogContent > div:not(#dialogButtons) input:not([type=radio]):not([type=checkbox]):not([type=button]),
#dialogContent > div:not(#dialogButtons) select,
#dialogContent > div:not(#dialogButtons) textarea {
  color: #313131;
  border: 1px solid #CECECE;
  background-color: #EFEFEF;
}

#dialogContent > div:not(#dialogButtons) input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:not(:focus):valid,
#dialogContent > div:not(#dialogButtons) input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:not(:focus):valid,
#dialogContent > div:not(#dialogButtons) select:not(select)[required]:not(:focus):valid,
#dialogContent > div:not(#dialogButtons) select:not(select)[pattern]:not(:focus):valid,
#dialogContent > div:not(#dialogButtons) textarea:not(select)[required]:not(:focus):valid, #dialogContent > div:not(#dialogButtons) textarea:not(select)[pattern]:not(:focus):valid {
  border-color: #00CE00;
  color: #070;
}

#dialogContent > div:not(#dialogButtons) input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[required]:not(:focus):invalid,
#dialogContent > div:not(#dialogButtons) input:not([type=radio]):not([type=checkbox]):not([type=button]):not(select)[pattern]:not(:focus):invalid,
#dialogContent > div:not(#dialogButtons) select:not(select)[required]:not(:focus):invalid,
#dialogContent > div:not(#dialogButtons) select:not(select)[pattern]:not(:focus):invalid,
#dialogContent > div:not(#dialogButtons) textarea:not(select)[required]:not(:focus):invalid, #dialogContent > div:not(#dialogButtons) textarea:not(select)[pattern]:not(:focus):invalid {
  border-color: #a00;
  color: #700;
}

@font-face {
  font-family: "Celestia";
  src: url("../font/Celestia Medium Redux.ttf");
  src: local("Celestia Redux Alternate"), local("Celestia Medium Redux"), url("../font/Celestia Medium Redux.ttf") format("truetype");
}

@media all and (max-width: 650px) {
  body {
    overflow: initial;
    background: #384c99 url("../img/grid-w-.1.svg");
  }
  body.sidebar-open {
    overflow: hidden;
  }
  header, footer {
    position: initial;
  }
  header nav {
    position: relative;
    box-shadow: 0 0 5px #384c99;
  }
  header nav:after {
    content: "MLPVC-RR";
    position: absolute;
    top: 0;
    lefT: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    display: block;
    text-align: center;
    font-size: 28px;
    line-height: 50px;
    color: #000;
    text-shadow: 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff, 0 0 0.133em #fff;
    font-family: 'Celestia', sans-serif;
  }
  header nav ul {
    position: relative;
    z-index: 2;
  }
  header nav ul li:not(:first-child) {
    display: none;
  }
  #sidebar {
    height: calc(100% - 50px);
  }
  #sidebar > .mobile-nav ul {
    list-style: none;
    padding: 0;
    margin: 5px 0 0;
  }
  #sidebar > .mobile-nav ul li {
    display: inline-block;
  }
  #sidebar > .mobile-nav ul li.active a {
    background-color: rgba(255, 255, 255, 0.5);
  }
  #sidebar > .mobile-nav ul li a {
    display: inline-block;
    padding: 2px;
    margin: 2px;
    color: #fff;
    border-radius: 5px;
  }
  #main {
    position: initial;
    padding: 10px 5px;
    background-color: #fff;
    background-image: url("../img/grid-1E3DB3-.2.svg"), url("../img/grid-1E3DB3-.1.svg");
  }
  footer {
    text-align: center;
  }
  .notice > label:first-child {
    text-align: center;
    font-size: 1.2em;
  }
}

@media all and (min-width: 650px) {
  body {
    overflow-x: auto;
    overflow-y: scroll;
    background-color: #fff;
    background-image: url("../img/grid-1E3DB3-.2.svg"), url("../img/grid-1E3DB3-.1.svg");
  }
  header, footer {
    z-index: 3;
    left: 0;
  }
  #main {
    position: relative;
    z-index: 1;
    padding: 8px 8px 46px 8px;
  }
  #sidebar {
    height: calc(100% - 50px - 38px);
  }
  #sidebar .mobile-nav {
    display: none;
  }
  footer {
    position: fixed;
    bottom: 0;
    height: 38px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: #384c99 url("../img/grid-w-.1.svg");
  }
  .notice > label:first-child {
    font-size: 1.4em;
  }
}

@media all and (min-width: 1200px) {
  #sidebar {
    background: rgba(0, 0, 0, 0.8) !important;
  }
  body.sidebar-open #sidebar {
    left: 0 !important;
  }
  body.sidebar-open #main {
    padding-left: calc(310px + 8px);
  }
  body.loading nav .sidebar-toggle {
    cursor: wait !important;
  }
  body.loading #sidebar {
    transition: none !important;
  }
  body.loading #main {
    transition: opacity 0.5s !important;
  }
}

.hidden {
  display: none;
}

.unreadable {
  font-size: 0;
}

:focus {
  outline: 0;
}

time[datetime][title] {
  cursor: help;
}

small {
  font-size: .75em;
}

[disabled], .disabled {
  cursor: not-allowed !important;
  opacity: .5 !important;
  pointer-events: none;
}

strong.typcn:before, span.typcn:not(:empty):before, em.typcn:before,
button:not(:empty):before, .btn:not(:empty):before {
  margin-right: 0.3125em;
}

img.avatar {
  width: 50px;
  height: 50px;
}

body {
  margin: 0;
  padding-top: 50px;
  font-family: 'Arimo', sans-serif;
}

header {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  color: #fff;
  box-sizing: border-box;
  background: #384c99 url("../img/grid-w-.1.svg");
}

header nav {
  display: block;
  height: 100%;
  line-height: 0;
  font-size: 0;
  white-space: nowrap;
}

header nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: block;
}

header nav ul li {
  display: inline-block;
  color: #fff;
  height: 22px;
  line-height: 22px;
  font-size: 18px;
  box-sizing: border-box;
  margin: 5px 0;
  vertical-align: middle;
}

header nav ul li:not(:first-child) {
  margin-left: 7px;
}

header nav ul li.active {
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

header nav ul li.active a:not(:link) {
  background-color: rgba(255, 255, 255, 0.2);
  transition: none;
  cursor: default;
}

header nav ul li.active a:not(:link):not(:first-child) {
  margin-left: 2px;
}

header nav ul li.sidebar-toggle {
  position: relative;
  margin: 2.5px;
  width: 45px;
  height: 45px;
  cursor: pointer;
  border: 2px solid white;
  border-radius: 100%;
  overflow: hidden;
}

header nav ul li.sidebar-toggle > .loader {
  position: relative;
  z-index: 2;
  display: none;
  width: 45px;
  height: 45px;
  top: -2px;
  left: -2px;
}

body.loading header nav ul li.sidebar-toggle > .loader {
  display: block;
}

header nav ul li.sidebar-toggle > .loader path {
  fill: #fff;
}

header nav ul li.sidebar-toggle > .avatar {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
}

body.loading header nav ul li.sidebar-toggle > .avatar {
  opacity: .5;
}

header nav ul li a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  padding: 1px 3.5px;
  height: 22px;
  line-height: 22px;
  border-radius: 3px;
  box-sizing: border-box;
  transition: background-color 0.2s linear;
}

header nav ul li a:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

header nav ul li a:active {
  background-color: rgba(255, 255, 255, 0.5);
}

header a[href^=http]:after, #sidebar .mobile-nav a[href^=http]:after {
  font-style: italic;
  content: "";
  width: 12px;
  height: 12px;
  background-image: url(../img/extlink.svg);
  background-size: 12px;
  display: inline-block;
  margin-left: 2px;
}

#main {
  position: relative;
  z-index: 1;
  width: 100%;
  box-sizing: border-box;
  transition: opacity 0.5s, padding-left 0.5s ease-in-out;
}

#main.locked {
  overflow: visible;
}

#main.pls-wait {
  opacity: .5;
  pointer-events: none;
  transition: none;
}

#content {
  border-radius: 5px;
  border: 1px solid rgba(170, 170, 170, 0.8);
  background-color: rgba(255, 255, 255, 0.6);
  padding: 10px;
  color: #000;
  box-sizing: border-box;
  width: 100%;
}

#content > h1 {
  font-size: 2em;
  text-align: center;
  margin: 0 0 5px;
  font-family: "Celestia", sans-serif;
}

#content > h1 + p {
  font-size: 1.6em;
  text-align: center;
  margin: 0 0 10px;
  font-style: italic;
}

#content a:not(.btn), #dialogContent a:not(.btn), .notice a:not(.btn) {
  color: #337287;
  text-decoration: none !important;
  transition: color 0.2s linear;
  font-weight: bold;
}

#content a:not(.btn):hover, #dialogContent a:not(.btn):hover, .notice a:not(.btn):hover {
  color: #4e96ae;
}

#sidebar {
  position: fixed;
  z-index: 2;
  top: 50px;
  left: -310px;
  width: 310px;
  max-width: 100%;
  box-sizing: border-box;
  color: #fff;
  background: rgba(0, 0, 0, 0.9);
  padding: 10px;
  text-align: center;
  transition: left 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
  overflow: auto;
}

body.sidebar-open #sidebar {
  left: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.9);
}

#sidebar > section {
  text-align: center;
}

#sidebar > section:not(:last-child) {
  margin-bottom: 15px;
}

#sidebar > section h2 {
  font-size: 24px;
  margin: 0 0 5px;
  font-family: "Celestia", sans-serif;
  font-weight: normal;
}

#sidebar > section p {
  margin: 0;
}

#sidebar > section .buttons > * {
  margin: 0;
}

#sidebar > section.welcome .links {
  list-style: none;
  border-radius: 5px;
  color: #777;
  background-color: #efefef;
  border: 1px solid;
  margin: 0 0 5px;
  padding: 5px;
}

#sidebar > section.welcome .links li {
  display: inline-block;
  white-space: normal;
}

#sidebar > section.welcome .links li a {
  text-decoration: none;
  color: inherit;
  border-bottom: 1px solid transparent;
  transition: border-bottom-color 0.2s linear;
}

#sidebar > section.welcome .links li:not(:last-child):after {
  content: ",";
  margin-right: .3em;
  display: inline-block;
}

#sidebar > section.welcome .links li:not(.active) a:hover:not(:active) {
  border-bottom-color: initial;
}

#sidebar > section.welcome .links li.active a {
  border-bottom-color: rgba(255, 255, 255, 0.5);
}

#sidebar > section.welcome .links:before {
  content: "Useful links";
