#sidebar > section.welcome .links:empty {
  display: none;
}

#sidebar #upcoming ul {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: left;
}

#sidebar #upcoming ul li {
  display: block;
  white-space: nowrap;
}

#sidebar #upcoming ul li:not(:last-child) {
  margin-bottom: 5px;
}

#sidebar #upcoming ul li > * {
  vertical-align: middle;
}

#sidebar #upcoming ul li .calendar {
  width: 40px;
  height: 40px;
  display: inline-block;
  position: relative;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
  background: #fafafa;
  text-align: center;
}

#sidebar #upcoming ul li .calendar > * {
  width: 100%;
  display: block;
  box-sizing: border-box;
}

#sidebar #upcoming ul li .calendar .top {
  top: 0;
  height: 15px;
  line-height: 15px;
  background: #f00 linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.1) 100%);
