  height: 25px;
  line-height: 25px;
  font-size: 1.5em;
  color: #000;
  border: 1px solid #777;
  border-top: 0;
}

#sidebar #upcoming ul li .meta {
  display: inline-block;
  margin: 2.5px 0 2.5px 5px;
  max-width: calc(100% - 45px);
}

#sidebar #upcoming ul li .title, #sidebar #upcoming ul li time, #sidebar #upcoming ul li .countdown {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}

#sidebar #upcoming ul li .title {
  font-size: 18px;
  height: 21px;
}

#sidebar #upcoming ul li time, #sidebar #upcoming ul li .countdown {
  font-size: 12px;
  height: 14px;
}

#sidebar #upcoming ul li time:before, #sidebar #upcoming ul li .countdown:before {
  content: "Airs ";
}

#sidebar #quote {
  display: block;
  margin: 0;
  opacity: 0;
  font-style: italic;
  line-height: 1.2em;
}

#sidebar #quote:empty {
  display: none;
}

#sidebar #quote:before {
  content: '\201C';
}

#sidebar #quote:after {
  content: '\201D';
}

#sidebar #quote[data-cite]:after {
  content: "”\a—" attr(data-cite);
  white-space: pre-wrap;
}

#sidebar .usercard {
  font-size: 0;
  color: #777;
  background-color: #efefef;
  border-radius: 4px;
  padding: 3px;
  margin: 0 0 5px;
  display: inline-block;
}

#sidebar .usercard .avatar-wrap {
  margin: 3px auto 5px;
  box-shadow: 0 0 5px;
  border: 1px solid;
}

#sidebar .usercard * {
  display: block;
}

#sidebar .usercard .un {
  font-size: 22px;
  letter-spacing: 1px;
}

#sidebar .usercard .un a {
  color: #337287;
  text-decoration: none !important;
  transition: color 0.2s linear;
  font-weight: bold;
}

#sidebar .usercard .un a:hover {
  color: #4e96ae;
}

#sidebar .usercard .role {
  font-size: 16px;
}

.avatar-wrap {
  position: relative;
  border-radius: 100%;
  border: 2px solid;
  margin: 0 auto 5px;
  width: 50px;
  height: 50px;
  cursor: default;
  user-select: none;
  color: #777;
  background-color: #efefef;
}

.avatar-wrap .avatar {
  border-radius: 100%;
}

.avatar-wrap .badge {
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  background-color: inherit;
  padding: 1px 2px;
  border-radius: 2px;
  line-height: 1em;
  font-size: 12px;
  border: 1px solid;
  height: 1em;
  text-transform: uppercase;
  font-weight: bold;
  pointer-events: none;
  box-shadow: inherit;
}

footer {
  display: block;
  width: 100%;
  padding: 10px;
  line-height: 18px;
  color: #fff;
  box-sizing: border-box;
}

footer a {
  color: inherit;
}

.color-blue {
  color: #07f;
}

.color-darkblue {
  color: #03d;
}

.color-green {
  color: #090;
}

.color-red {
  color: #d00;
}

.color-orange {
  color: #d50;
}

.color-yellow {
  color: #b80;
}

.notice {
  display: block;
  margin: 0;
  border: 2px solid;
  border-radius: 7px;
  color: #fff;
  background-color: #000;
  padding: 7.5px;
  font-size: 16px;
}

.notice:not(:last-child) {
  margin-bottom: 10px;
}

.notice.info {
  color: #07f;
  background-color: #def;
}

.notice.info button, .notice.info .btn {
  color: #def;
  background-color: #07f;
}

.notice.success {
  color: #090;
  background-color: #dfd;
}

.notice.success button, .notice.success .btn {
  color: #dfd;
  background-color: #090;
}

.notice.fail {
  color: #d00;
  background-color: #fdd;
}

.notice.fail button, .notice.fail .btn {
  color: #fdd;
  background-color: #d00;
}

.notice.warn {
  color: #d50;
  background-color: #fed;
}

.notice.warn button, .notice.warn .btn {
  color: #fed;
  background-color: #d50;
}

.notice.caution {
  color: #b80;
  background-color: #ffd;
}

.notice.caution button, .notice.caution .btn {
  color: #ffd;
  background-color: #b80;
}

.notice > label:first-child {
  display: block;
  padding-bottom: 5px;
  margin-bottom: 10px;
  font-weight: bold;
  border-bottom: 2px solid;
}

.notice > p {
  margin: 0 !important;
}

.notice > p:not(:last-child) {
  margin-bottom: 5px;
}

button, .btn {
  display: inline-block;
  margin: 5px;
  border: 0;
  border-radius: 3px;
  color: #fff;
  background-color: #777;
  padding: 4px 7px;
  font-size: 16px;
  text-decoration: none;
  user-select: none;
  opacity: .8;
  cursor: pointer;
  transform-origin: 50% 100%;
  transition: transform 0.1s linear, opacity 0.2s linear;
}

button.large, .btn.large {
  font-size: 22px;
}

button.blue, .btn.blue {
  color: #def !important;
  background-color: #07f;
}

button.darkblue, .btn.darkblue {
  color: #ddf !important;
  background-color: #03d;
}

button.green, .btn.green {
  color: #dfd !important;
  background-color: #090;
}

button.red, .btn.red {
  color: #fdd !important;
  background-color: #d00;
}

button.orange, .btn.orange {
  color: #fed !important;
  background-color: #d50;
}

button.yellow, .btn.yellow {
  color: #ffd !important;
  background-color: #b80;
}

button:empty, .btn:empty {
  font-size: .8em;
  line-height: 1em;
  width: 1em;
  height: 1em;
  box-sizing: initial;
  padding: 4px;
  margin: 0;
}

button:hover, button:focus, .btn:hover, .btn:focus {
  opacity: 1;
}

button:active, .btn:active {
  transform: scale(0.96);
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

ul.pagination {
  display: block;
  padding: 0;
  margin: 10px 0;
  list-style: none;
  text-align: center;
}

ul.pagination:last-child {
  margin-bottom: 0;
}

ul.pagination:before {
  content: "Pages: ";
}

ul.pagination li {
  display: inline;
}

ul.pagination li:not(:last-child):after {
  content: ', ';
}
